import React, { useContext, useState } from 'react';
import { CustomThemeContext } from '../themes/CustomThemeProvider';

import cpu from '../images/chip-memory.svg';
// MuI Components
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import { Menu, MenuItem } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import { useHistory, useLocation } from 'react-router-dom';
import { sourceCodeSetter, getExampleSourceCode } from './context';

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  titleBar: {
    flexGrow: 1,
  },
  title: {
    fontWeight: 800,
    cursor: 'pointer',
    fontSize: 30,
  },
  navbar: {
    padding: 5,
  },
  info: {
    marginTop: 2,
  },
}));

function Navbar() {
  const classes = useStyles();
  const history = useHistory();
  const loc = useLocation();
  const { currentTheme, setTheme } = useContext(CustomThemeContext);
  const handleThemeChange = (event) => {
    if(currentTheme === 'normal') {
      setTheme('dark');
    } else {
      setTheme('normal');
    }
  };

  const [sourcesMenuAnchorEl, setSourcesMenuAnchorEl] = useState(null);
  const sourcesMenuOpen = Boolean(sourcesMenuAnchorEl);

  const showSourcesMenu = (event) => {
    setSourcesMenuAnchorEl(event.currentTarget);  
  }

  const closeSourcesMenu = () => {
    setSourcesMenuAnchorEl(null);
  }

  const testSourceCode = (example) => {
    return () => {
      sourceCodeSetter(getExampleSourceCode(example));
      closeSourcesMenu();
    }
  }

  return (
    <div className={classes.root}>
      <AppBar
        color='secondary'
        position='static'
        id='navbarCustomId'
        className={classes.navbar}
        style={
          currentTheme === 'dark'
            ? {
                boxShadow:
                  '0px 4px 6px -1px rgba(255,255,255,0.3), 0px 4px 5px 0px rgba(255,255,255,0.24), 0px 1px 10px 0px rgba(255,255,255,0.22)',
              }
            : null
        }
      >
        <Toolbar>
          <div className={classes.titleBar}>
            <Typography
              color='primary'
              component='span'
              onClick={() => history.push('/8086-emulator-web/')}
              className={classes.title}
            >
              Эмулятор процессора 8086
            </Typography>
          </div>
          {loc.pathname === '/8086-emulator-web/compile' ? (
            <IconButton onClick={() => history.push('/8086-emulator-web/help')}>
              <span
                style={
                  currentTheme === 'normal'
                    ? { fontWeight: 'bold' }
                    : { color: '#ccc' }
                }
                className={classes.info}
              >
                &#9432;
              </span>
            </IconButton>
          ) : null}
          {loc.pathname === '/8086-emulator-web/help' ? (
            <IconButton
              onClick={() => history.push('/8086-emulator-web/compile')}
            >
              <img
                style={
                  currentTheme === 'dark'
                    ? { filter: 'invert(0.7)', height: '2rem', rotate: '90deg' }
                    : { height: '2rem', rotate: '90deg' }
                }
                src={cpu}
                alt='compiler'
              />
            </IconButton>
          ) : null}

          <IconButton onClick={ showSourcesMenu }>
            [+]
          </IconButton>
          <Menu 
            anchorEl={ sourcesMenuAnchorEl }
            open={ sourcesMenuOpen }
            onClose={ closeSourcesMenu }
          >
            <MenuItem onClick={ testSourceCode("ex1") }>Задание A</MenuItem>
            <MenuItem onClick={ testSourceCode("ex2") }>Задание B</MenuItem>
            <MenuItem onClick={ testSourceCode("ex3") }>Задание C</MenuItem>
            <MenuItem onClick={ testSourceCode("ex4") }>Задание D</MenuItem>
            <MenuItem onClick={ testSourceCode("ex5") }>Задание E</MenuItem>
            <MenuItem onClick={ testSourceCode("ex6") }>Задание F</MenuItem>
          </Menu>

          { /* <IconButton
            onClick={handleThemeChange}
            id='themeBtn'
            style={{ marginRight: -20 }}
          >
            <img
              style={currentTheme === 'dark' ? { filter: 'invert(1)' } : null}
              src='data:image/png;base64,'
              alt='change theme icon'
            />
          </IconButton> */}
        </Toolbar>
      </AppBar>
    </div>
  );
}

export default Navbar;