
var sourceCodeSetter = null;

function setSourceCodeCallback(value) {
    if (sourceCodeSetter != value) {
        sourceCodeSetter = value;
        console.log(value);
    }
};

const ex1 = 
`; Задание A
;
; Измените программу следующим образом:
;   * входные данные: AX = 13, BX = 28, CX = 37
;   * выходные данные: регистр DX содержит сумму чисел AX, BX и CX

; точка входа в программу
start:
    mov ax, 13      ; записать число 13 (0x0d) в регистр AX
    mov bx, 28      ; записать число 28 (0x1c) в регистр BX
    add ax, bx      ; сложить AX и BX (результат лежит в AX)
    mov dx, ax      ; скопировать значение AX в DX
`;

const ex2 = `
; Задание B
;
; Измените программу следующим образом:
;   * ВХОД : ячейки памяти x и y размера word 
;   * ВЫХОД: в ячейку памяти z записан результат вычисления
;            арифметического выражения x*x + 5*y - 7

x:  dw  8
z:  dw  0

; Данная программа вычисляет значение z = 2*x - 1

start:
    ; (1) запишем значение x в регистр AX:
    
    mov bx, offset x    ; записать в BX адрес ячейки с маркером x
    mov ax, word [bx]   ; записать в AX слово, расположенное по 
                        ; адресу, находящемуся в регистре BX
    
    ; (2) вычислим 2*x:
    
    mov cx, 2
    mul cx      ; CX умножается на значение AX 
                ; и результат помещается в AX (!)
    
    ; (3) вычтем 1:
    
    sub ax, 1
    
    ; (4) и запишем результат в ячейку с маркером z
    mov bx, offset z
    mov word [bx], ax
`;

const ex3 = 
`; Задание C
;
; Измените программу следующим образом:
;   * ВХОД : ячейки памяти x и y размера word 
;   * ВЫХОД: в ячейке z записать минимальное из чисел x и y

x:  dw  0x44
y:  dw  0x33
z:  dw  0

; Данная программа выбирает наибольшее из чисел x и y
; и записывает его в ячейку памяти z

start:

    mov si, offset x
    mov ax, word [si]
    mov si, offset y
    mov bx, word [si]
    mov di, offset z

    cmp ax, bx          ; сравнить AX и BX
    jg  set_first       ; если AX > BX, перейти к метке set_first
    mov word [di], bx   ; тут AX <= BX, записываем значение BX в z
    jmp end             ; обязательно "перепрыгиваем" через метку!
set_first:
    mov word[di], ax    ; тут AX > BX, так что его и пишем
    
end:
`;

const ex4 = `
; Задание D
;
; Измените программу следующим образом:
;   * ВХОД : ячейки памяти x и y размера word 
;   * ВЫХОД: если верно хотя бы одно из двух условий: 
;                    x < 10 ИЛИ y <= 2*x
;               * записать в ячейку памяти z значение 0xff, 
;               * в противном случае, записать в z значение 0x33

x:  dw  11
y:  dw  7
z:  dw  0

; Данная программа по смыслу эквивалентна следующей на C++:
;
;   if (y < x && x >= 10) { z = 0xaa; } else { z = 0xbb; }

start:

    mov si, offset x
    mov ax, word [si]
    mov si, offset y
    mov bx, word [si]
    mov di, offset z
    
    cmp bx, ax
    jge set_false
    cmp ax, 10
    jl  set_false
    
    mov word [di], 0xaa
    jmp after_true

set_false:
    mov word [di], 0xbb
    
after_true:
    `;

const ex5 = `
; Задание E
;
; Измените данную программу таким образом, чтобы она подсчитывала
; сумму квадратов нечётных чисел, не превышающих n. Результат должен
; быть записан в ячейку s.
;
; Например, для n = 8 (или n = 7) необходимо подсчитать сумму:
;
;          1*1 + 3*3 + 5*5 + 7*7


n:  dw  5
s:  dw  0

; Данная программа подсчитывает сумму чисел от 1 до n и записывает 
; результат в ячейку памяти s

start:

    mov si, offset n
    mov bx, word [si]
    
    mov cx, 1
    mov ax, 0

sum_loop:
    add ax, cx
    inc cx
    cmp cx, bx
    jbe sum_loop
    
    mov di, offset s
    mov word [di], ax
`;

const ex6 = `
; Задание F
;
; Измените данную программу таким образом, чтобы она находила
; МАКСИМУМ среди всех однобайтовых беззнаковых целых чисел,
; записанных последовательно в ячейках памяти, начиная с маркера vals
; и заканчивая маркером last (не включая саму ячейку last).
;
; Результат должен быть записан в ячейку памяти s.
;
vals:   db 0x12
        db 0x34 
        db 0x85 
        db 0x11
        db 0x36
last:   db 0        ; маркер конца массива чисел
s:      db 0

; Данная программа находит СУММУ среди всех однобайтовых беззнаковых
; целых чисел, записанных в ячейках памяти, начиная с маркера vals 
; и заканчивая маркером last (не включая его).

start:
    mov si, offset vals
    mov cx, offset last 
    
    mov ax, 0       ; инициализируем сумматор - регистр AX
    mov bx, 0       ; обнуляем регистр BX, чтобы использовать BL
                    ; для хранения текущего элемента массива
iterate:
    mov bl, byte [si]
    add ax, bx
    inc si          ; переходим к следующей ячейке памяти
    cmp si, cx      ; сравниваем адрес текущей ячейки si с адресом конца cx
    jb iterate      ; если si < cx, переходим к смещению iterate
    
    mov di, offset s    ; записываем в di адрес ячейки с результатом
    mov word [di], ax   ; записываем сумму из AX в ячейку с результатом
`;

// https://github.com/GoPavel/assembler-course/blob/master/lection.md

function getExampleSourceCode(example) {
    return {
        'ex1': ex1,
        'ex2': ex2,
        'ex3': ex3,
        'ex4': ex4,
        'ex5': ex5,
        'ex6': ex6
    }[example] || `< Пример "${example}" отсутствует >`;
}

export { 
    sourceCodeSetter, 
    setSourceCodeCallback,
    getExampleSourceCode
};